
#list {
    position:absolute;
    display:flex;
    cursor:text;
    
    user-select: none;

    font-family: var(--font-family);
    
    top:var(--list-top);
    left:var(--list-left);

    width:var(--list-width);
    height:var(--list-height);

    contain: layout paint;
    overflow: hidden;
}

#content {
    position:absolute;
    top:0;
    left:0;
    width:var(--content-width);
    height:var(--content-height);

    content-visibility: auto;
    contain-intrinsic-size: var(--list-width) var(--list-height);
}
